// Movie Info Total Container
.movie-info-container {
  height: 80vh;
  width: 100vw;
  background-repeat: no-repeat;
  background-size: cover;
}

/* Banner */
.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25vh;
  background: #1a0d0d;
  position: relative;
  overflow: hidden;

  .backdrop {
    opacity: 0.4;
    width: 100vw;
  }

  .movie-title {
    position: absolute;
    font-size: 4vw;
    text-align: center;
    width: 90%;
    color: white;
    font-family: 'Allan';
    text-shadow: 5px 5px 10px #000000;
  }
}

/* Movie Info Section */
.movie-info {
  display: grid;
  grid-template-columns: 40% 60%;
  height: 100%;
}

/* Left Poster Side */
.info-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.poster {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 75%;
  width: 60%;

  img {
    max-height: 100%;
    max-width: 100%;
    border-radius: 5px;
  }

  .tagline {
    margin-top: 2vw;
    font-size: 1.5vw;
    color: white;
    text-shadow: 0.5px 2px black;
    font-weight: 700;
  }
}

/* Right Info Side */
.info-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // align-items: flex-start;
  margin-top: 5%;
  margin-bottom: 5%;
  height: 100%;
  color: white;

  p {
    font-size: 1.3vw;
  }
}

.right-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 85%;
  padding: 2vw;
  background-color: rgba(0, 0, 0, 0.623);
  border-radius: 5px;
}

.genre-section {
  font-size: 1.3vw;
}

.genre-tags {
  display: flex;
  flex-direction: row;
}

.genre {
  background-color: rgba(120, 0, 0, 0.6);
  font-size: 1.3vw;
  margin: 2.5%;
  border-radius: 3px;
  width: 10vw;
  height: 2.5vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 11vw;
  height: 2.5vw;
  margin-top: 1.5vw;
  align-self: center;
  font-size: 1.2vw;
  background-color: rgba(0, 0, 0, 0.906);
  border: 2px solid white;
  border-radius: 3px;
  color: white;
  text-decoration: none;
  font-weight: bold;

  &:hover {
    background-color: rgba(255, 0, 0, 0.392);
    color: white;
  }
}

.trailer {
  padding-top: 15%;
  width: 100vw;
}

li {
  list-style-type: none;
}

@media screen and (max-width: 1024px) {
  .trailer {
    position: relative;
    overflow: hidden;
    padding-top: 56.25%;
  }

  .trailer iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }

  .movie-info {
    grid-template-columns: 1fr;
  }

  .movie-info-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .info-right {
    margin-bottom: 20vh;

    p {
      font-size: 2vh;
    }
  }

  .info-left {
    .tagline {
      font-size: 3vh;
    }
  }

  .genre-section {
    font-size: 2vh;
  }

  .genre {
    font-size: 1.4vh;
  }
}
