.title {
  text-decoration: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 20vh;
  width: 100%;
  background-color: #5e0000;
  color: white;
  margin-bottom: 15px;
  font-family: 'Allan';
  font-size: 80px;
  font-weight: 700;
  text-shadow: 2px 2px 10px #000000;
}

.tomato-logo {
  height: 11vh;
  padding: 3vh;
}

@media screen and (max-width: 1024px) {
  .title {
    height: 14vh;
    font-size: 55px;
  }

  .tomato-logo {
    height: 8vh;
  }
}

@media screen and (max-width: 768px) {
  .title {
    height: 17vh;
    font-size: 40px;
  }
  .tomato-logo {
    height: 6vh;
  }
}

@media screen and (max-width: 600px) {
  .title {
    height: 11vh;
    font-size: 30px;
    margin-bottom: 15px;
    width: 100%;
  }
  .tomato-logo {
    height: 4vh;
  }
}
